.container{
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    padding-top: 8%;
    padding-left: 2%;
    padding-right: 2%;
}
h1{
    font-size: 4rem;
    background: linear-gradient(90deg, rgb(255, 229, 190) 0%, rgba(246,185,59,1) 100%);
    -webkit-background-clip: text;
	-webkit-text-fill-color: transparent;
}
@media only screen and (max-width: 1000px) {
    h1{
        font-size: 3rem;
    }
  }
@media only screen and (max-width: 600px) {
    h1{
        font-size: 2rem;
    }
  }


